'use strict';
var ko = require('knockout');
var $ = require('jquery');
var Diner = require('./diner');

var VeiwModel = function(config) {
	var self = this;

	this.addDiner = function(formElement) {
		event.preventDefault();
		var newDinerName = $(formElement).children('#new-diner-name').val();
		self.validateDinerName(formElement, newDinerName);
	};

	this.validateDinerName = function(formElement, newDinerName) {
		if (newDinerName) {
			var newdiner = new Diner(newDinerName, self);
			$(formElement).children('#new-diner-name').val('');
			self.diners.push(newdiner);
			return;
		}
		alert('invalid diner name.');
	};

	this.diners = ko.observableArray([]);

	this.subtotalAll = ko.computed(function() {
		var subtotal = 0;
		var length	= self.diners().length;
		$.each(self.diners(), function(key, value) {
			$.each(value.dishes(), function(key, value) {
				subtotal += Number(value.dishPrice());
			});
		});
		subtotal = Number(subtotal).toFixed(2);
		return subtotal;
	}, this);

	this.taxPrecent = ko.observable(config.taxPrecent);

	this.tipPrecent = ko.observable(config.tipPrecent);

	this.taxTotalAll = ko.computed(function() {
		var taxTotal = self.subtotalAll()*config.taxPrecent/100;
		taxTotal = Number(taxTotal).toFixed(2);
		return taxTotal;
	}, this);

	this.tipTotalAll = ko.computed(function() {
		var tipTotal = Number(self.subtotalAll());
		tipTotal += Number(self.taxTotalAll());
		tipTotal = tipTotal*config.tipPrecent/100;
		tipTotal = Number(tipTotal).toFixed(2);
		return tipTotal;
	}, this);

	this.totalTotalAll = ko.computed(function() {
		var total = Number(self.subtotalAll());
		total += Number(self.taxTotalAll());
		total += Number(self.tipTotalAll());
		total = Number(total).toFixed(2);
		return total;
	}, this);

	this.showOrder = function() {
		$('.column-left').show();	
		$('.column').hide();
		$('.column-right').hide();
	};
		
	this.showFinalBill = function() {
		$('.column-left').hide();	
		$('.column').show();
		$('.column-right').hide();
	};
	
	this.showIndivdual = function() {
		$('.column-left').hide();	
		$('.column').hide();
		$('.column-right').show();
	};

};

module.exports = VeiwModel;


